import { Navbar, Container, Nav } from "react-bootstrap";
import { Button } from "react-bootstrap";
import "./App.css";
import { lazy, Suspense, createContext, useEffect, useState } from "react";
import data from "./data.js";
import { Routes, Route, Link, useNavigate, Outlet } from "react-router-dom";
// import Detail from "/routes/Detail.js";
// import Cart from "./routes/Cart.js";
import axios from "axios";
import { useQueries, useQuery, useQueryClient } from "@tanstack/react-query";

const Detail = lazy(() => import("./routes/Detail.js"));
const Cart = lazy(() => import("./routes/Cart.js"));


export let Context1 = createContext(); // context 

function App() {

  // :    
  useEffect(() => {
    if (localStorage.getItem("watched") === null) {
      localStorage.setItem("watched", JSON.stringify([]));
    }
  }, []);

  // Move localStorage operations to useEffect to prevent infinite re-renders
  useEffect(() => {
    let obj = { name: "kim" };
    localStorage.setItem("data", JSON.stringify(obj));
    let str = localStorage.getItem("data");
    console.log(str);
  }, []);

  const [shoes, setShoes] = useState(data);
  const [clickCount, setClickCount] = useState(0); //    

  let [] = useState([10, 11, 12]);

  let navigate = useNavigate();

  let result = useQuery({
    queryKey: ["getName"],
    refetchOnWindowFocus : false,
    retry : 10,
    queryFn: () => {
      return axios.get("https://codingapple1.github.io/userdata.json").then(a => a.data);
    },
  });


  function Event() {
    return (
      <div>
        <h4>  !!! </h4>
        <Outlet></Outlet>
      </div>
    );
  }

  const handleLoad = () => {
    const nextCount = clickCount + 1;
    setClickCount(nextCount);

    let url = "";
    if (nextCount === 1) url = "https://codingapple1.github.io/shop/data2.json";
    if (nextCount === 2) url = "https://codingapple1.github.io/shop/data3.json";

    if (!url) return; // 3    

    axios
      .get(url)
      .then(res => {
        const copy = [...shoes, ...res.data];
        setShoes(copy);
      })
      .catch(() => {
        console.log("  ");
      });
  };
  const [name, setName] = useState("");
  return (
    <div className="App">

      <input onChange={e => setName(e.target.value)} />
      {/* <Routes>
        <Route
          path="/"
          element={
            <>
              <div
                className="main-bg"
                style={{ backgroundImage: `url(${maru})` }}
              />
              <div className="container">
                <div className="row">
                  {shoes.map((item, i) => (
                    <Card
                      key={item.id ?? i}
                      shoes={item}
                      i={i + 1} //   1 
                    />
                  ))}
                </div>
              </div>
            </>
          }
        />
        <Route path="/detail" element={<div>~~~</div>} />
      </Routes> */}

      <div></div>
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="#home">Shopping mall</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link
              onClick={() => {
                navigate("/");
              }}
            >
              Home
            </Nav.Link>{" "}
            |
            <Nav.Link
              onClick={() => {
                navigate("/detail/0");
              }}
            >
              {" "}
              Detail
            </Nav.Link>{" "}
            |
            <Nav.Link
              onClick={() => {
                navigate("/about");
              }}
            >
              About
            </Nav.Link>{" "}
            |
            <Nav.Link
              onClick={() => {
                navigate("/event");
              }}
            >
              Event
            </Nav.Link>{" "}
            |
            <Nav.Link
              onClick={() => {
                navigate("/cart");
              }}
            >
              Cart
            </Nav.Link>{" "}
            |
          </Nav>
          <nav className="ms-auto user-status">
            {result.isPending && ''}
            {result.isError && ''}
            {result.isSuccess && result.data.name}
          </nav>
        </Container>
      </Navbar>

      <Routes>
        <Route
          path="/"
          element={
            <>
              <div className="main-bg"></div>
              <div className="container">
                <div className="row">
                  {shoes.map((item, i) => (
                    <Card
                      key={item.id ?? i}
                      shoes={item}
                      i={i + 1} //   1 
                      navigate={navigate}
                    />
                  ))}
                </div>
              </div>

              <button
                onClick={() => {
                  const nextCount = clickCount + 1;
                  setClickCount(nextCount);

                  let url = "";
                  if (nextCount === 1) {
                    url = "https://codingapple1.github.io/shop/data2.json";
                  } else if (nextCount === 2) {
                    url = "https://codingapple1.github.io/shop/data3.json";
                  } else return;

                  axios
                    .get(url)
                    .then(res => {
                      const copy = [...shoes, ...res.data];
                      setShoes(copy);
                    })
                    .catch(() => {
                      console.log("  ");
                    });
                }}
              >
                
              </button>

              {clickCount >= 3 && <p>   .</p>}
            </>
          }
        ></Route>
        <Route
          path="/detail/:id"
          element={
            <Context1.Provider value={{ , shoes }}>
              <Suspense fallback={<div>...</div>}>
              <Detail shoes={shoes} />
              </Suspense>
            </Context1.Provider>
          }
        ></Route>

        <Route path="/about" element={<About />}>
          <Route path="member" element={<div> </div>} />
          <Route path="location" element={<About />} />
        </Route>

        <Route path="/event" element={<Event />}>
          <Route path="one" element={<div>   </div>} />
          <Route path="two" element={<div>  </div>} />
        </Route>

        {/* <Route path="/about/member" element={<About />}></Route> */}

        <Route path="/cart" element={<Cart />}></Route>

        <Route path="*" element={<div>  ~~ 404!</div>}></Route>
      </Routes>
    </div>
  );
}

function About() {
  return (
    <div>
      <h4>  . . . . .</h4>
      <Outlet></Outlet>
    </div>
  );
}

function Card({ shoes, i, navigate }) {
  return (
    <div className="col-md-4" onClick={() => navigate(`/detail/${shoes.id}`)} style={{ cursor: "pointer" }}>
      <img src={`https://codingapple1.github.io/shop/shoes${i}.jpg`} width="80%" alt={shoes.title} />
      <h4>{shoes.title}</h4>
      <p>{shoes.price}</p>
    </div>
  );
}

export default App;
